<template>
    <div class="cats group" id="category">
        <ul class="category ac-custom ac-checkbox ac-checkmark">
            <li class="subtitle">
                <h3>Categories</h3>
                <span v-if="stillLoading" class="loader"></span>
                <span v-else class="filter"></span>
            </li>
            <span v-for="(category, index) in categories" :key="`category-${index}`">
                <li v-if="index < 3">
                    <input type="checkbox" :id="category.id" :value="category.name" :checked="false" v-on:input="category.name = $event.target.value">
                    <label :for="category.id">{{category.name}}</label>
                </li>
            </span>
        </ul>
        <ul class="category ac-custom ac-checkbox ac-checkmark">
            <span v-for="(category, index) in categories" :key="`category-${index}`">
                <li v-if="index > 2">
                    <input type="checkbox" :id="category.id" :value="category.name" :checked="false" v-on:input="category.name = $event.target.value">
                    <label :for="category.id">{{category.name}}</label>
                </li>
            </span>
        </ul>

    </div>
</template>

<script>
import db from './database/databaseInit'
export default {
    name : 'Categories',
    data () {
        return {
        categories : [],
        stillLoading : true
        }
    },
    created(){
        const cats = [];
        db.collection('tg-testimonial').get().then(data =>{
            data.forEach(doc => {
                const data = {
                    'id' : doc.id,
                    'name' : doc.data().category
                }
                cats.push(data);
            });
            function removeDuplicates(myArr, prop) {
                    return myArr.filter((obj, pos, arr) => {
                    return arr.map(mapObj =>
                    mapObj[prop]).indexOf(obj[prop]) === pos;
                });
            }           
            this.categories = removeDuplicates(cats,"name")
            //console.log(this.categories);
        })
    },
    updated(){
        this.stillLoading = false
    },
    methods: {
        
    }
    
}
</script>



