<template>
  <div id="app">
    <div class="main group">
    <div class="left group">

      <div class="logo group">
        <router-link to="/">
          <img src="./assets/img/logo.png" alt="Testimonial Generator">
        </router-link>
      </div>

      <h2 class="title group">
				<span class="highlight">Amazing Testimonial</span> for your business or profession
			</h2>

      <Categories />

    </div>
    <div class="right group">
      <transition name="fadeIn">
				<router-view></router-view>
      </transition>	
    </div>
  </div>
  <TestimonialEdit />
	<Footer />
  </div>
</template>

<script>
import Categories from "./components/Categories"
import Footer from "./components/Footer"
import TestimonialEdit from "./components/TestimonialEdit"

export default {
	name: 'App',
	components:{
		Categories,
    Footer,
    TestimonialEdit
	}
}

</script>




