<template>
  <div class="cards group" id="quotesBlock">
    <div class="loader stack__item" v-bind:class="{ active: isLoading }">
      <div class="quote">
        <span class="qt"></span>
        <h3>Please wait. Your Testimonial are being generated !!</h3>
      </div>
      <span class="author">Testimonial Generator</span>
    </div>
    <ul id="stack_yuda" class="stack stack--yuda">
      <li class="stack__item" v-for="(card, index) in cardsData" :key="`card-${index}`">
        <span class="copy-btn" v-clipboard="card.testimonial + ' - ' + card.client" v-clipboard:success="OnCopy" v-clipboard:error="OnError">copy</span>
				<div class="quote" :id="card.category"><span class="qt"></span><h3>{{card.testimonial}}</h3></div>
				<span class="author">{{card.client}}</span>
        <span class="copy-btn customize" :id="card.id" data-dialog="somedialog">Customize</span>
			</li>
    </ul>
    <div class="controls">
      <button class="button button--sonar button--reject button--default" data-stack="stack_yuda">Next</button>
      <h3>or press <span class="highlight">spacebar</span></h3>
    </div>
  </div>
</template>

<script>
import db from './database/databaseInit'
import { functions } from 'firebase';

export default {
    name : 'TestimonialCards',
    data () {
        return {
          cardsData : [],
          unFiltered : [],
          isLoading : true,
          customizeData : [],
          eventAttached : false,
          evt :0,
          up :0
        }
    },
    created(){      
        db.collection('tg-testimonial').get().then(data =>{
            let t = [];
            data.forEach(doc => {
                const data = {
                    'id' : doc.id,
                    'category' : doc.data().category,
                    'testimonial': doc.data().testimonial,
                    'client' : doc.data().client
                }
                t.push(data);
            });
            var shuffle = function(a) {
                var j, x, i;
                for (i = a.length; i; i--) {
                    j = Math.floor(Math.random() * i);
                    x = a[i - 1];
                    a[i - 1] = a[j];
                    a[j] = x;
                }
            };
            shuffle(t);
            this.cardsData = t;
            this.unFiltered = t;
        });
        
    },
    updated(){
      //if(!this.eventAttached){
        this.init();
      this.enableFilter();   

      //}
      
      this.dialogInit();
    },
    methods: {
      init (){
        function mobilecheck() {
          var check = false;
          (function(a){if(/(android|ipad|playbook|silk|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows (ce|phone)|xda|xiino/i.test(a)||/1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(a.substr(0,4)))check = true})(navigator.userAgent||navigator.vendor||window.opera);
          return check;
        }

        this.isLoading = false;

        var clickeventtype = mobilecheck() ? 'touchstart' : 'click';

        var yuda = new Stack(document.getElementById('stack_yuda'));

        document.querySelector('.button--reject[data-stack = stack_yuda]').addEventListener(clickeventtype, function() { yuda.reject(); });
        
        window.onkeydown = function(e) {
            if(e.keyCode == 32 && e.target == document.body) {
                e.preventDefault();
                yuda.reject(); 
                return false;
            }
        };
      },
      enableFilter (){
        let self = this;
        let selectedCategories = [];
        let selectFilters = document.querySelectorAll('input[type="checkbox"]');
        let isFilterEnabled = false;


        selectFilters.forEach((checkbox) => {
          checkbox.addEventListener("change", function(){
              selectedCategories = [];
              let checked =  document.querySelectorAll('input[type="checkbox"]:checked');
          
              if(checked.length > 0){
                checked.forEach((i) => {
                  selectedCategories.push(i.value);
                });
              } 
              if(selectedCategories.length > 0){
                    let t = [];
                    self.unFiltered.forEach(data =>{
                      if(selectedCategories.includes(data.category))
                        t.push(data);
                    });
                    self.cardsData = t;
              } else {
                self.cardsData = self.unFiltered
              }

              self.eventAttached = true

          });
        });
        
      },
      dialogInit(){
        let self = this;
        var dlgtrigger = document.querySelectorAll( '.customize' ),
        somedialog = document.getElementById("editDialog"),
        dlg = new DialogFx( somedialog );
        dlgtrigger.forEach( dl =>{
          dl.addEventListener( 'click', e =>{
            self.customizeData[0] = e.target.id;
            self.customizeData[1] = e.target.previousElementSibling.innerText;
            self.customizeData[2] = e.target.previousElementSibling.previousElementSibling.querySelector("h3").innerText;
            self.customizeData[3] = e.target.previousElementSibling.previousElementSibling.id;
            //console.log(self.customizeData);
            document.querySelector(".image h3").innerText = self.customizeData[2];
            document.querySelector(".image p").innerText = self.customizeData[1];
            document.querySelector("[name='docid']").value = self.customizeData[0];
            document.querySelector("[name='cat']").value = self.customizeData[3];
            document.querySelector("[name='testimonial']").value = self.customizeData[2];
          });
          dl.addEventListener( 'click', dlg.toggle.bind(dlg) );        
        });
      },
      OnCopy ({ value, event }) {
        //console.log(event.target.innerText)
        event.target.innerText = "copied!"
        setTimeout(()=> {
          event.target.innerText = "copy";
        },2000)
      },
      OnError ({ value, event }) {
        //console.log('error', value)
      }
    }

}
</script>


